.servicios{
    margin-top: 20px;
    
    position: relative;

    h2{
        font-weight: $bold;
    }

.imagen-servicios{
    background-image: url("../img/servicios.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 1000px;
    width: 100%;
    
    @include telefono-mini {
        height: 750px;   
    }
    @include telefono {
        height: 600px;
        // height: 600px; 
    }
    
}

.texto-servicios{
    // margin: auto;
    // width: 100%;
    position: absolute;
    top: 130px;
    left: 25px;
    margin-right: 30px;

    @include tablet {
       
        left: 50px;
        margin-right: 30px; 
    }
    @include desktop {
       
        left: 150px;
        margin-right: 30px; 
    }
 
    

    .servicio{
        
        
        h3{
            text-align: start;
            margin: 0;

            span{
                font-weight: $black;
                color: $celesteOscuro;
            }
        }
        p{
            font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
            background-color: $celesteOpacidad;
            border-radius: 5px;
            padding: 10px;
            color: $blanco;
            border: 1px solid $verdeCeleste;
        }
    }
}

}