.beneficios{
    margin-top: 6rem;
    @include grid(1,1);

    @include tablet {
        @include grid(2,1);
        
    }
    @include desktop {
        @include grid(4,1);
        
    }
}

.beneficio{
    text-align: center;
    img{
        width: 30%;
        margin: auto;
        @include desktop {
            width: 50%;
            
        }
    }

    h4{
        color: $verdeCeleste;
        // padding: 5px 0;
        margin: 5px 0;
    }

}

