.galeria{
    margin: 10rem auto;
    padding: 0px 10px;

    @include grid(1,2rem);

    @include tablet {
        @include grid(2,2rem);
       
    }

    @include desktop {
        @include grid(3,2rem);
    }
}

.imagen-geleria{
    position: relative;
    transition: 1s all ease-in-out;

    .texto-info{
        position: absolute;
        // bottom: 4rem;
        top: 10rem;
        // top: 30%;
        background-color: $celesteOpacidad;
        width: 100%;
        // height: 100%;
        transition: 1s all ease-in-out;        

        h3{
            margin: 0px;
            color: white;
            font-size: 2.6rem;           
        }
        
        
        p{
            display: none;
           
        }

        
    }
    &:hover .texto-info{
        bottom: 0;
        top: 0;
        // background-color: $celesteOpacidad;
        
        background-color: $plomoOpacidad;
        transition: 1s all ease-in-out;

        p{
            display: block;
            transition: 1s all ease-in-out;
            text-align: justify;
            margin: 1rem;
            font-size: 1.4rem;
            color: $blanco;
        }

        }

        
        @include desktop {
            &:hover h3{
                font-size: 2rem;
                font-weight: $bold;
                color: $celeste;
            }
    
        }
    
}

