.trabajo-galeria{
    padding: 20px;
  
    @include grid(1, 15px);
    @include tablet{
        
        @include grid(2, 15px);
    }
    @include desktop{
        
        @include grid(3, 15px);
    }

    .galerias{
        border: 2px solid $gris;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        margin-top: 20px;
        // border-radius: 15px;
       
        .titulo, .precio{
            margin-bottom: 0px;
            margin-top: 0px;
            padding: 10px 20px;
        }
        .titulo{
            font-weight: $bold;
        }
        .precio{
            color: $verde;
        }
        button{
            display: flex;
            justify-content: center;
            width: 100%;
            padding: 15px;
            background-color: $verdeCeleste;
            color: white;
            border: 0px;
        }
    }

    
}

