/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

header {
  margin: 0;
  padding: 0;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 1000px;
  margin: auto;
}

.nosotros {
  display: flex;
  flex-direction: column;
}
@media (min-width: 1024px) {
  .nosotros {
    display: grid;
    gap: 10px;
    grid-template-columns: repeat(2, 1fr);
    flex-direction: row;
  }
}
@media (min-width: 1024px) {
  .nosotros .logo-nosotros {
    display: flex;
    align-items: center;
  }
}
.nosotros .info-nosotros {
  margin: 0px 20px;
  text-align: justify;
  background-color: rgb(44, 53, 148);
  padding: 10px 25px;
  border-radius: 20px 20px 0px 20px;
}
.nosotros .info-nosotros p {
  color: white;
}

.trabajo-galeria {
  padding: 20px;
  display: grid;
  gap: 15px;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .trabajo-galeria {
    display: grid;
    gap: 15px;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .trabajo-galeria {
    display: grid;
    gap: 15px;
    grid-template-columns: repeat(3, 1fr);
  }
}
.trabajo-galeria .galerias {
  border: 2px solid #b3b3b3;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin-top: 20px;
}
.trabajo-galeria .galerias .titulo, .trabajo-galeria .galerias .precio {
  margin-bottom: 0px;
  margin-top: 0px;
  padding: 10px 20px;
}
.trabajo-galeria .galerias .titulo {
  font-weight: 700;
}
.trabajo-galeria .galerias .precio {
  color: #71B100;
}
.trabajo-galeria .galerias button {
  display: flex;
  justify-content: center;
  width: 100%;
  padding: 15px;
  background-color: #19227b;
  color: white;
  border: 0px;
}

.barra {
  display: flex;
  justify-content: space-between;
}

.logo {
  font-family: "Tangerine", cursive;
  font-size: 3.4rem;
  color: rgb(40, 31, 207);
  margin: 0px;
  padding: 0px;
  width: 150px;
}

.navegacion {
  display: flex;
  flex-direction: column;
}
@media (min-width: 768px) {
  .navegacion {
    flex-direction: row;
  }
}
.navegacion .menu-movil {
  display: flex;
  flex-direction: column;
  align-items: end;
  margin-top: 10px;
  margin-right: 10px;
}
@media (min-width: 1024px) {
  .navegacion .menu-movil {
    display: none;
  }
}
@media (min-width: 768px) {
  .navegacion .menu-movil {
    display: none;
  }
}
.navegacion .menu-movil span {
  width: 25px;
  margin: 2px;
  border: 2px solid black;
}
.navegacion .opciones {
  display: flex;
  display: none;
  flex-direction: column;
  transition: 2s all ease-in-out;
}
@media (min-width: 768px) {
  .navegacion .opciones {
    display: block;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
}
.navegacion .opciones a {
  padding: 0px 20px;
  font-weight: 700;
  color: #19227b;
  transition: 1s all ease-in-out;
}
.navegacion .opciones a:hover {
  transition: 1s all ease-in-out;
  border-bottom: 1px solid #19227b;
}

@media (min-width: 768px) {
  .movil {
    display: flex !important;
  }
}

.carrusel {
  overflow: hidden;
  position: relative;
}
.carrusel .imagenes {
  width: 600%;
  display: flex;
  transform: translate(0%);
  transition: 1s all ease-in-out;
}
.carrusel .imagenes img {
  width: 16.6666666667%;
}
.carrusel .texto {
  position: absolute;
  top: -20%;
  left: 10px;
}
@media (min-width: 280px) {
  .carrusel .texto {
    top: -20%;
  }
}
@media (min-width: 400px) {
  .carrusel .texto {
    top: 10%;
  }
}
@media (min-width: 768px) {
  .carrusel .texto {
    top: 20%;
  }
}
@media (min-width: 1024px) {
  .carrusel .texto {
    top: 20%;
    left: 170px;
  }
}
.carrusel .texto h1 {
  color: #FFFFFF;
  width: 90%;
  background-color: rgba(30, 43, 106, 0.6392156863);
  border-radius: 8px;
  font-size: 2.3rem;
  padding: 10px;
}
@media (min-width: 280px) {
  .carrusel .texto h1 {
    font-size: 2rem;
  }
}
@media (min-width: 768px) {
  .carrusel .texto h1 {
    font-size: 4rem;
    font-weight: 900;
  }
}
@media (min-width: 1024px) {
  .carrusel .texto h1 {
    width: 70%;
    font-size: 5rem;
    font-weight: 900;
  }
}
.carrusel .texto .boton {
  display: flex;
  justify-content: center;
  width: 200px;
  background-color: #19227b;
  padding: 10px 25px;
  color: #FFFFFF;
  margin-bottom: 10px;
  transition: 1s all ease-in-out;
}
.carrusel .texto .boton:hover {
  transform: scale(1.1);
}

.beneficios {
  margin-top: 6rem;
  display: grid;
  gap: 1;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .beneficios {
    display: grid;
    gap: 1;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .beneficios {
    display: grid;
    gap: 1;
    grid-template-columns: repeat(4, 1fr);
  }
}

.beneficio {
  text-align: center;
}
.beneficio img {
  width: 30%;
  margin: auto;
}
@media (min-width: 1024px) {
  .beneficio img {
    width: 50%;
  }
}
.beneficio h4 {
  color: #19227b;
  margin: 5px 0;
}

.servicios {
  margin-top: 20px;
  position: relative;
}
.servicios h2 {
  font-weight: 700;
}
.servicios .imagen-servicios {
  background-image: url("../img/servicios.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 1000px;
  width: 100%;
}
@media (min-width: 280px) {
  .servicios .imagen-servicios {
    height: 750px;
  }
}
@media (min-width: 400px) {
  .servicios .imagen-servicios {
    height: 600px;
  }
}
.servicios .texto-servicios {
  position: absolute;
  top: 130px;
  left: 25px;
  margin-right: 30px;
}
@media (min-width: 768px) {
  .servicios .texto-servicios {
    left: 50px;
    margin-right: 30px;
  }
}
@media (min-width: 1024px) {
  .servicios .texto-servicios {
    left: 150px;
    margin-right: 30px;
  }
}
.servicios .texto-servicios .servicio h3 {
  text-align: start;
  margin: 0;
}
.servicios .texto-servicios .servicio h3 span {
  font-weight: 900;
  color: rgba(9, 23, 26, 0.6392156863);
}
.servicios .texto-servicios .servicio p {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
  background-color: rgba(30, 43, 106, 0.6392156863);
  border-radius: 5px;
  padding: 10px;
  color: #FFFFFF;
  border: 1px solid #19227b;
}

.galeria {
  margin: 10rem auto;
  padding: 0px 10px;
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 768px) {
  .galeria {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .galeria {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}

.imagen-geleria {
  position: relative;
  transition: 1s all ease-in-out;
}
.imagen-geleria .texto-info {
  position: absolute;
  top: 10rem;
  background-color: rgba(30, 43, 106, 0.6392156863);
  width: 100%;
  transition: 1s all ease-in-out;
}
.imagen-geleria .texto-info h3 {
  margin: 0px;
  color: white;
  font-size: 2.6rem;
}
.imagen-geleria .texto-info p {
  display: none;
}
.imagen-geleria:hover .texto-info {
  bottom: 0;
  top: 0;
  background-color: rgba(0, 0, 0, 0.7607843137);
  transition: 1s all ease-in-out;
}
.imagen-geleria:hover .texto-info p {
  display: block;
  transition: 1s all ease-in-out;
  text-align: justify;
  margin: 1rem;
  font-size: 1.4rem;
  color: #FFFFFF;
}
@media (min-width: 1024px) {
  .imagen-geleria:hover h3 {
    font-size: 2rem;
    font-weight: 700;
    color: rgb(73, 130, 243);
  }
}

.portada-imagen {
  background-image: url("../img/fondo.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  height: 500px;
  width: 100%;
  background-attachment: fixed;
}
.portada-imagen .informacion {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0px 10px 0px 20px;
}
.portada-imagen .informacion h4 {
  color: #FFFFFF;
}
.portada-imagen .informacion button {
  padding: 10px 25px;
  background-color: #19227b;
  border: none;
  color: white;
  transition: 1s all ease-in-out;
}
.portada-imagen .informacion button:hover {
  transform: scale(1.2);
}

.fonto-portada {
  background-color: rgba(9, 23, 26, 0.6392156863);
  height: 100%;
}

footer {
  margin-top: 7rem;
  background-color: #181818;
  padding: 50px 0;
}
footer .informacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: auto;
}
@media (min-width: 768px) {
  footer .informacion {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
}
footer .informacion .logo {
  font-family: "Tangerine", cursive;
  font-size: 3.4rem;
  color: rgb(40, 31, 207);
}
footer .informacion .opciones {
  display: flex;
  align-items: center;
}
footer .informacion .opciones a {
  margin: 0px 10px;
  color: #FFFFFF;
}
footer .informacion .opciones a:hover {
  border-bottom: 2px solid #b3b3b3;
}

.redes {
  display: flex;
  align-items: center;
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .redes {
    margin-top: 0;
    margin-right: 2rem;
  }
}
.redes a i {
  margin: 0px 5px;
  font-size: 3rem;
}
.redes a i:nth-child(1) {
  color: rgb(67, 67, 245);
}
.redes a i:nth-child(1):hover {
  transform: scale(1.2);
}
.redes a:nth-child(2) i {
  border-radius: 8px;
  background-color: #d6249f;
  background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%, #d6249f 60%, #285AEB 90%);
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.25);
}
.redes a:nth-child(2) i:hover {
  transform: scale(1.2);
}

.red-fija {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
}
.red-fija i {
  color: #22851c;
  font-size: 5rem;
}