.portada-imagen{
    background-image: url("../img/fondo.jpg");
    background-repeat: no-repeat;
    background-size: cover;
    height: 500px;
    width: 100%;
    background-attachment: fixed;
    

    .informacion{
        height: 100%;
        width: 100%;
        // background-color: $celesteOpacidad;
        display:flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        padding: 0px 10px 0px 20px;

        h4{
            color: $blanco;
        }

        button{
            padding: 10px 25px;
            background-color: $verdeCeleste;
            border: none;
            color: white;
            transition: 1s all ease-in-out;

            &:hover{
                transform: scale(1.2) ;
            }
        }
    }
}

.fonto-portada{
    background-color: $celesteOscuro;
    height: 100%;
}