.nosotros{
    display: flex;
    flex-direction: column;
    @include desktop {
        @include grid(2, 10px);
        flex-direction: row;
 
    }


    .logo-nosotros{
      @include desktop {
        display: flex;
        align-items: center;
 
    }
    }

    .info-nosotros{
        margin: 0px 20px;
        text-align: justify;
        background-color: rgb(44, 53, 148);
        padding: 10px 25px;
        border-radius: 20px 20px 0px 20px;
        
        p{
            color: white;
        }
    }
}