.barra{
    display: flex;
    justify-content: space-between;

   

}

.logo{
   font-family: $fuenteLogo;
   font-size: 3.4rem;

    color: rgb(40, 31, 207);
     margin: 0px;
     padding: 0px;
     width: 150px;
 }

.navegacion{
   display: flex;
   flex-direction: column;
//    justify-content: end;
   @include tablet {
    flex-direction: row;
   }

    .menu-movil{
       display: flex;
       flex-direction: column;
    //    justify-content: end;
       align-items: end;
       margin-top: 10px;
       margin-right: 10px;

       @include desktop {
        display: none;
       }

       @include tablet {
        display: none;
       }

        span{
         width: 25px;
         margin: 2px;
         border: 2px solid black;
        }
    
    }

    
    .opciones{
        display: flex;
        display: none;
        flex-direction: column;
        transition: 2s all ease-in-out;
    
           @include tablet {
            display: block;
            display: flex;
            flex-direction: row;
            justify-content: center;
            align-items: center;
           }


           a{
            padding: 0px 20px;
            font-weight: $bold;
            color: $verdeCeleste;
            transition: 1s all ease-in-out;
             &:hover{
                transition: 1s all ease-in-out;
                border-bottom: 1px solid $verdeCeleste;
             }
           }
    }
   
}

.movil{
   @include tablet {
     display: flex!important;
   }
}
