.carrusel{
    overflow: hidden;
    position: relative;
    .imagenes{
        width: 600%;
        display: flex;
        transform: translate(0%);
        // 16.666
        // 33.333333
        // 49.999
        // 66.665
        // 83.331
        // 100
        transition: 1s all ease-in-out;
        // height: 90%;
      
     img{
       width: calc(100% / 6);
     }
    }

    .texto{
      position: absolute;
      top: -20%;
      left: 10px; 

      @include telefono-mini {
        top: -20%;
    }
    @include telefono {
      top: 10%;
      
    }
    @include tablet {
      top: 20%;
      
    }
     
      
      @include desktop {
      top: 20%;
      left: 170px; 
      }

      h1{
        color: $blanco;
        width: 90%;
        background-color: $celesteOpacidad;
        border-radius: 8px;
        font-size: 2.3rem;
        padding: 10px;

        @include telefono-mini {
          font-size: 2rem;
      }
      @include tablet {
        font-size: 4rem;
        font-weight: $black;
    }
        @include desktop {
            width: 70%;
            font-size: 5rem;
            font-weight: $black;
          }
      }

      .boton{
        display: flex;
        justify-content: center;
        width: 200px;
        background-color: $verdeCeleste;
        padding: 10px 25px;
        color: $blanco;
        margin-bottom: 10px;
        transition: 1s all ease-in-out;

        &:hover{
         transform: scale(1.1);
        }
    }
    }

}


