footer{
    margin-top: 7rem;
    background-color: $plomo;
    padding: 50px 0;

    .informacion{
        display: flex;
        flex-direction: column;
        // justify-content: space-around;
        // justify-content: center;
        align-items: center;
        margin: auto;

        @include tablet {
            flex-direction: row;
            justify-content: space-between;
            align-items: center;
        }

        .logo{
            font-family: $fuenteLogo;
            font-size: 3.4rem;
            color: rgb(40, 31, 207);
        }

        .opciones{
            display: flex;
            align-items: center;

            a{
                margin: 0px 10px;
                color: $blanco;

                &:hover{
                    border-bottom: 2px solid $gris;
                }
            }
        }

       
    }
}

