.redes{
    display: flex;
    align-items: center;
    margin-top: 2rem;

    @include tablet {
        margin-top: 0;
        margin-right: 2rem;
    }

    a{
    
    i{
        margin: 0px 5px;
       font-size: 3rem;
        &:nth-child(1){
            color: rgb(67, 67, 245);

            &:hover{
                transform: scale(1.2);
            }
        }
        
      
    }

    &:nth-child(2){
        i{
        border-radius: 8px;
        background-color: #d6249f;
        background: radial-gradient(circle at 30% 107%, #fdf497 0%, #fdf497 5%, #fd5949 45%,#d6249f 60%,#285AEB 90%);
        box-shadow: 0px 3px 10px rgba(0,0,0,.25);

        &:hover{
            transform: scale(1.2);
        }
        }
        
    }
       

    }
}


.red-fija{
    position: fixed;
    bottom: 2rem;
    right: 2rem;
    i{
        color: #22851c;
        font-size: 5rem;
    }
}